import React, { useEffect } from "react";
import { Outlet, useNavigate } from "react-router";
import { Link } from "react-router-dom";
import { ReactComponent as Back } from "../../../../assets/icon/arrow-left-solid.svg";
import { ReactComponent as DouzoneLogo } from "../../../../assets/lg-douzone-blue.svg";
import useNavigateGuard from "../../../../hooks/useNavigateGuard";
import { useSelector } from "react-redux";
import { ToastContainer } from "react-toastify";

function LayoutAuthMain() {
  const isAuth = useSelector((state) => state.auth);
  useNavigateGuard("login", isAuth);

  const navigate = useNavigate();
  const onBack = () => {
    navigate(-1);
  };

  return (
    <div className="container">
      <Back className="w-[20px] fill-douzone" onClick={onBack}></Back>
      <div className="mb-5 flex justify-center">
        <Link to={"/"}>
          <DouzoneLogo className="h-10 w-full p-2"></DouzoneLogo>
        </Link>
      </div>
      <Outlet />
      <ToastContainer
        position="bottom-center"
        autoClose={2000}
        hideProgressBar
        newestOnTop
        closeOnClick
        rtl={false}
        pauseOnFocusLoss={false}
        draggable
        pauseOnHover={false}/>
    </div>
  );
}

export default LayoutAuthMain;
